import { createContext, ReactNode, FC } from "react";
export const ObserverContext = createContext<IntersectionObserver|null>(null);

interface ObserverProps {
    setActiveSection : (value:string)=> void;
    children: ReactNode;
}
const Observer: FC<ObserverProps> = ({setActiveSection , children})=>{
    const observer = new IntersectionObserver(
        (entries) => {
          const visibleSection = entries.find(
            (entry) => entry.isIntersecting
          )?.target.id;
          if (visibleSection) setActiveSection(visibleSection);
        },
        { root: null, threshold: 0.5 } // When 0.5 of section visible
      );
    return(
    <ObserverContext.Provider value={observer}>
        {children}
    </ObserverContext.Provider>)
}
export default Observer;