import { useContext, useEffect, useRef , FC} from "react";
import { ObserverContext } from "./Observer";

import { Button, Box } from "@mui/material";
import {sections} from"./Sections";
// Components for each section

interface PageLoaderProps {
    activeSection : string;
    
}

const PageLoader:FC<PageLoaderProps> = ({activeSection}) => {
  const observerContext = useContext(ObserverContext); //Dependency Injection (Get the observer instance)
  const sectionRefs = useRef<(HTMLElement | null)[]>([]);

  useEffect(() => {
    if (!observerContext) return; // Ensure the observer exists
    const sectionsSnapshot = [...sectionRefs.current];

    sectionsSnapshot.forEach((section) => {
      if (section) observerContext.observe(section); // Use the observer instance
    });

    return () => {
        sectionsSnapshot.forEach((section) => {
        if (section) observerContext.unobserve(section); // Unobserve on cleanup
      });
    };
  }, [observerContext]);

  return (
    <div>
      
    <Box
    sx={{
      position: "fixed",
      display: "flex",
      flexDirection:{
         xs: "row",
         lg:"column"
      },
      top: {
        xs: "unset",
        lg:"50%"
    },
      left: {
        xs:"50%",
        lg: 0
      },
      bottom:{
        xs:0,
        lg:"unset"

      },
      transform:{ 
        xs:"translateX(-50%)",
        lg:"translateY(-50%)"
      },
      backgroundColor: "rgba(0, 0, 0, 0.1)",
      height: "auto", 
      padding: 1, 
      boxShadow: 3, 
      borderRadius: "1rem",
      margin: "auto"
    }}
  >
        {sections.map(({ name , icon:Icon}) => (
          <Button
            key={name}
            sx={{
            
              backgroundColor: activeSection === name.toLowerCase() ? "yellow" : "white",
              width: "4rem",
              height: "4rem",
              borderRadius: "2rem",
              margin:"0.3rem"
            }}
            href={`#${name.toLowerCase()}`} // Link to section
          >
            <Icon fontSize="large"/>
          </Button>
        ))}
      
    </Box>


    <div>
    {sections.map(({ name, component: Component }, index) => ( //React Component should be named as SentanceCase
          <section
            key={name}
            id={name.toLowerCase()} // Add id to match href
            ref={(el) => (sectionRefs.current[index] = el)} // Use ref for intersection tracking
          >
            <Component/>
          </section>
        ))}
    </div>
  </div>
  );
};

export default PageLoader;